.main {
  height: 100%;
}

.main-screen {
  padding-bottom: 100px;
}

.fotter {
  position: absolute;
  bottom: 50px;
  right: 80px;
  width: 6rem;
  height: 6rem !important;
  /* width: 70px; */
  border-radius: 50% !important;
}

.fotter-back {
  position: absolute;
  bottom: 50px;
  left: 50px;
  width: 6rem;
  height: 6rem !important;
  /* width: 70px; */
  border-radius: 50% !important;
}

.footer-btn {
  margin: 0 15px !important;
  border: 0;
  color: white !important;
  width: 100%;
  height: 100%;
  border-radius: 50% !important;
}

.footer-btn-back {
  margin: 0 15px !important;
  width: 100%;
  height: 100%;
  border: 0;
  background-color: #f7f8fe !important;
  box-shadow:  18px 18px 36px #c4cad2,
              -18px -18px 36px #ffffff !important;
  color: black !important;
  border-radius: 50% !important;
}

.footer-btn-back:hover {
  background: linear-gradient(145deg, #dedfe5, #ffffff);
}

.footer-btn-back:active {
  background: #f7f8fe;
  box-shadow: inset 4px 4px 8px #c1c1c6,
              inset -4px -4px 8px #ffffff;
}

.Title-elements {
  margin-bottom: 2rem !important;
}

.SizePic {
  width: 70%;
  margin-top: 1rem;
}

@media only screen and (max-width: 991px) {
  .SizePic {
    margin-bottom: 2rem;
  }
}

.title {
  font-weight: 700!important;
}