* {
  box-sizing: border-box;
}

.main-box {
  overflow: hidden;
  position: relative;
  margin: auto;
  background-color: #f7f8fe;
  height: 100%;
  display: flex;
}

.container {
  box-sizing: border-box;
  margin: auto;
  padding: 3rem auto;
}

/* .main {
  margin-left: 260px;
} */

.steps {
  position: relative;
  border-radius: 10px;
  background-color: #f7f8ff !important;
  border: 3px solid #c4cad2;
}

.allItems {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 3rem;
}

.main-screen {
  width: 90% !important;
  text-align: center !important;
  margin: auto !important;
}

.title {
  text-align: center !important;
  margin-bottom: 1rem !important;
  margin-top: 0.5rem !important;
}

.subTitle {
  text-align: center !important;
  margin-left: auto !important;
  margin-right: auto !important;
  margin-bottom: 1rem !important;
  margin-top: 0.5rem !important;
}

/* HIDE RADIO */
[type="radio"] {
  position: absolute;
  opacity: 0;
  width: 0;
  height: 0;
}

/* IMAGE STYLES */
[type="radio"] + img {
  cursor: pointer;
}

/* CHECKED STYLES */
label {
  display: inline;
}
