.head {
  background-color: #f7f8ff !important;
  padding: 10px 0;
}

.head-wraper {
  display: flex !important;
  flex-flow: row nowrap !important;
  margin: auto 150px;
  padding: 0 15px;
  max-width: 1140px;
  align-items: center;
  position: relative;
}

@media only screen and (max-width: 991px) {
  .head-wraper {
    display: block !important;
    height: 75px;
    margin: auto 5px;
    padding: 0 5px;
  }
}

.header-title-wraper {
  position: absolute;
  right: 0;
  left: 0;
}

.header-title {
  margin: auto !important;
  color: black !important;
}

.header-logo {
  padding: 0;
  display: inline-flex;
}

.header-logo img {
  vertical-align: middle;
}


