.item-img {
  height: 50px;
  display: block;
  width: 100%;
  position: relative;
  margin: 10px auto;
}

.item input {
  position: absolute;
  display: none;
}

button,
input {
  overflow: visible;
}

.item-img img {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  max-width: 100%;
  max-height: 100%;
  /* filter: invert(85%) sepia(12%) saturate(148%) hue-rotate(174deg) brightness(94%) contrast(91%); */
}

img {
  display: inline-block;
  border: 0;
  vertical-align: middle;
  border-style: none;
}