.App {
  background-color: #f7f8fe !important;
  height: 100%;
}

.invoice {
  margin: 5rem auto;
  max-width: 760px;
  width: 90%;
  display: flex;
  flex-direction: column;
  background-color: #f7f8fe !important;
  border: 3px solid #c4cad2;
  border-radius: 10px;
}

.items {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding: 0.8rem;
}

.headLine {
  display: flex;
  align-content: center;
  justify-content: flex-start;
  align-items: center;
  border-bottom: 3px solid #c4cad2;
}

.head-title {
  margin-left: 1rem !important;
  align-items: center;
}

.icon {
  margin-left: 5%;
  width: 40px;
}

.revType {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.items-head {
  display: flex;
  align-items: center;
}

.items-head-title {
  text-align: left;
  margin-left: 1rem !important;
  padding-bottom: 0.9rem;
}

.conf-item {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.price-container {
  font-size: 150%;
}

.price-container > span {
  font-family: Verdana;
  font-weight: bold;
  font-size: 120%;
  color: #4050b5;
}

.footer-confirm {
  display: flex;
  justify-content: center;
  padding: 1rem;
}

.footer-conf {
  margin: auto 0.5rem
}