@mixin transition {
	transition: all 120ms ease-out 0s;
}

.itemForCheck {
  float: left;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  cursor: pointer;
  margin: 2rem;
  height: 8rem;
	width: 8rem;
  $border-radius: 2.4rem;
  border-radius: $border-radius;
	border: 0;
  background: #f7f8fe;
  box-shadow:  18px 18px 36px #c4cad2,
              -18px -18px 36px #ffffff;
	@include transition;

  &:hover {
    background: linear-gradient(145deg, #dedfe5, #ffffff);
  }

  &:active {
    background: #f7f8fe;
    box-shadow: inset 18px 18px 36px #c1c1c6,
                inset -18px -18px 36px #ffffff;
  }
}


label {
  position: relative;
  cursor: pointer;

  & input[type="checkbox"] {
    transition: box-shadow 0.3s ease-in-out;
    transition: background-color 0.3s ease-in-out;
    transition: background-image 0.3s ease-in-out;
    position: absolute;
    opacity: 0;
  }

  & :checked ~ .itemForCheck {
    background: #f7f8fe;
    box-shadow: inset 18px 18px 36px #c1c1c6,
                inset -18px -18px 36px #ffffff;
  }
}

.item-subtitle {
  font-size: 1rem;
  text-transform: capitalize;
}
