.itemForCheck-img {
  height: 100px;
  display: block;
  width: 100%;
  position: relative;
}

.itemForCheck-subtitle {
  color: white;
  font-size: 19px;
  line-height: 23px;
  margin-top: 10px;
  margin-bottom: 0;
}

.itemForCheck input {
  position: absolute;
  display: none;
}

.itemForCheck-img img {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  max-height: 80%;
  max-width: 80%;
}

img {
  display: inline-block;
  border: 0;
  vertical-align: middle;
  border-style: none;
}

